#root, body, html {
  height: 100%;
  widht: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  height: 100%;
  width: 100%;
}

.App-link {
  color: #61dafb;
}

.App-sidebar {
  width:30%;
  height:100vh;
  background-color: #efefef;
  overflow-y: auto;
}

.App-sidebar h3 {
  display: inline-block;
}

.App-sidebar a {
  display: block;
  margin-bottom: 0.5rem;
  height: 100%;
  white-space:nowrap;
  text-overflow:ellipsis;
  overflow:hidden;
}

.App-content {
  width:100%;
  padding:0 20rem 0 5rem;
  overflow:auto;
}

.Question {
  margin-bottom: 1rem;
}

.Question-header {
  display:flex;
  align-items: center;
  justify-content: space-between;
  position:relative;
}

.Question-text {
  width:70%;
}

.Question-delete {
  position: absolute;
  top: 10%;
  right: 0;
}

.Question-form {
  margin-top:2rem;
}

.Question-form legend {
  font-size: 1.4rem;
  font-weight: bold;
}

.Question-form div {
  margin-top:1rem;
}

.Question-form input {
  margin-left: 0.5rem;
}

.Question-form button {
  margin-top: 0.5rem;
}

.Question-answers div {
  margin-top: 0.5rem;
}

.Question-answers div button {
  margin-left: 0.5rem;
}
.Form div {
  margin-top:1rem;
}

.Form input {
  margin-left: 0.5rem;
}

.Form legend {
  font-size: 1.4rem;
  font-weight: bold;
}

.Form textarea {
  display:block;
  margin-top: 0.2rem;
  resize: vertical;
}

.Form button {
  margin-top: 0.5rem;
}
